/* Path */
.vg-path {
  stroke: black;
  stroke: var(--vg-path-color);
  stroke-width: 0.5mm;
  stroke-width: var(--vg-path-width);
  stroke-opacity: 1;
  stroke-opacity: var(--vg-path-opacity);
  stroke-linecap: none;
  stroke-linejoin: round;
  stroke-dasharray: none;
