}
.vg-path-dashed {
  --vg-path-dasharray: 2mm 1mm;
}

.vg-axis {
  --vg-path-color: var(--vg-axis-color, black);
  stroke: #222222;
  stroke: var(--vg-axis-color, black);
  stroke-width: 0.3mm;
  stroke-width: var(--vg-axis-width);
  marker-end: url(#vg-axis-marker-farrowfwd);
}
.vg-grid {
  stroke: #888888;
