  stroke-width: 0.7mm;
  stroke-width: var(--vg-asymptote-width);
  stroke-dasharray: 2mm 1mm;
}
.vg-segment {
  marker-start: var(--vg-marker-point);
  marker-mid: none;
  marker-end: var(--vg-marker-point);
}
.vg-ray {
  marker-start: var(--vg-marker-point);
  marker-mid: none;
  marker-end: var(--vg-marker-arrowfwd);
}
.vg-line {
  marker-start: var(--vg-marker-arrowrev);
  marker-mid: none;
  marker-end: var(--vg-marker-arrowfwd);
}
.vg-polygon {
  marker-start: var(--vg-marker-point);
  marker-mid: var(--vg-marker-point);
  marker-end: var(--vg-marker-point);
}
.vg-scatterplot {
  stroke: none;
  marker-start: var(--vg-marker-point);
  marker-mid: var(--vg-marker-point);
  marker-end: var(--vg-marker-point);
}
.vg-fillregion {
  fill: black;
