}

.vg-mark { fill: black; fill: var(--vg-path-color); stroke-width: 0.5mm; stroke-width: var(--vg-path-width) }
.vg-point { marker-start: var(--vg-marker-point) }
.vg-hole { marker-start: var(--vg-marker-hole) }

