  border-color: #ccc;
}

button:focus, input:focus, select:focus, textarea:focus {
  outline: none;
}

button:active, input:active, select:active, textarea:active {
  outline: none;
}

button:hover {
  background-image: -webkit-linear-gradient(white, 50%, #eee);
  background-image: linear-gradient(white, 50%, #eee);
  background-image: var(--ui-gradient-v-2, linear-gradient(white, 50%, #eee));
}

button:active {
  color: white;
  border-color: black;
  background-image: -webkit-linear-gradient(bottom, #27f, 50%, #05f);
  background-image: linear-gradient(0deg, #27f, 50%, #05f);
}

button, button:disabled {
  background-image: -webkit-linear-gradient(white, 50%, #f5f5f5);
  background-image: linear-gradient(white, 50%, #f5f5f5);
  background-image: var(--ui-gradient-v-1, linear-gradient(white, 50%, #f5f5f5));
}

input[type=number] {
  max-width: 3em;
}

pre, code {
  font-family: monospace;
  font-family: var(--ui-font-family-monospace, monospace);
}


.editable > *:hover {
  outline: 3px dotted lightgreen;
  outline: 3px dotted var(--edit-color-light, lightgreen);
  background-color: var(--edit-color-faded);
}

.preview-area {
  border: 1px solid #ccc;
  border: 1px solid var(--ui-border-1, #ccc);
  background-color: white;
  background-color: var(--ui-page, white);
  padding: 5mm;
  border-radius: 3px;
  font-size: 1.1em;
  max-width: 11in;
}

.text-preview-output > * {
  margin: 0;
}

.text-preview-input > textarea {
  min-width: 4in;
  padding: 2mm;
  margin: 0;
  min-height: 4em;
}

.text-preview-output {
  border: 1px solid #ccc;
  border: 1px solid var(--ui-border-1, #ccc);
  background-color: white;
  background-color: var(--ui-page, white);
  padding: 2mm;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border-radius: 3px;
}

html, body {
  height: 100%;
  width: 100%;
  margin: 0;
  overflow: hidden;
  font-size: 10pt;
}

body {
  height: 100%;
  width: 100%;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
